	module.exports = {"data":{"site":{"siteMetadata":{"title":"Justin Ball","author":"Justin Ball"}},"markdownRemark":{"id":"/Users/jbasdf/projects/justinball/src/posts/2008-02-03-payday-lenders-suck/index.md absPath of file >>> MarkdownRemark","html":"<p>I was just reading in the <a href=\"http://deseretnews.com/article/1,5143,695249792,00.html\">Deseret News this morning that payday lenders have filed 27,000 lawsuits in the State of Utah since 2005</a>.  In the Provo judicial district this account for 81% of the total lawsuits.  It is bad enough that these places prey on the poor with 500% interest loans.  It is yet another of their sins that they use the court system, funded with tax money, as their goons to collect.  Who needs Guido and concrete boots when you can get the same service for free?</p>\n<p>These guys will burn in hell along with <a href=\"/2008/02/01/email-sucks/\">Intermedia's Servers</a></p>","frontmatter":{"title":"Payday Lenders Suck","date":"February 03, 2008","imageUrl":null,"structuredHtml":null,"image":null}}},"pathContext":{}}

